export default {
  props: {
    currentStep: Number,
    fields: Object,
    updateOptions: Function
  },
  data: () => ({
    category: 1,
    customMessages: {
      required: "Выберете {_field_}",
    },
    categories: [
      {
        id: 1,
        name: "{radio-option #1}",
      },
      {
        id: 2,
        name: "{radio-option #2}",
      },
      {
        id: 3,
        name: "{radio-option #3}",
      },
    ],
    products: [
      {
        id: 1,
        name: "{product #1}",
        priceMax: 0,
        priceMin: 0,
        thumbnail: require("@/assets/car.jpg"),
      },
      {
        id: 2,
        name: "{product #2}",
        priceMax: 0,
        priceMin: 0,
        thumbnail: require("@/assets/car.jpg"),
      },
      {
        id: 3,
        name: "{product #3}",
        priceMax: 0,
        priceMin: 0,
        thumbnail: require("@/assets/car.jpg"),
      },
      {
        id: 4,
        name: "{product #1}",
        priceMax: 0,
        priceMin: 0,
        thumbnail: require("@/assets/car.jpg"),
      },
      {
        id: 5,
        name: "{product #2}",
        priceMax: 0,
        priceMin: 0,
        thumbnail: require("@/assets/car.jpg"),
      },
      {
        id: 6,
        name: "{product #3}",
        priceMax: 0,
        priceMin: 0,
        thumbnail: require("@/assets/car.jpg"),
      },
    ],
  }),
  methods: {
    getProductName(product) {
      return product ? product.name : null;
    },
    updateOptionProduct() {
      const optionProduct = {
        type: "product",
        name: this.stepFields.product.name,
        value: this.getProductName(this.stepFields.product.value),
        stepId: this.currentStep
      };

      this.updateOptions(optionProduct);
    },
  },
  computed: {
    stepFields: {
      get() {
        return this.fields;
      },
      set(value) {
        this.$emit("update:fields", value);
      },
    },
  },
};
