export default {
  props: {
    currentStep: Number,
    fields: Object,
    updateOptions: Function,
  },
  data: () => ({
    cities: [
      "Уфа1",
      "Уральск1",
      "Увельский1",
      "Ульяновск1",
      "Уфа",
      "Уральск",
      "Увельский",
      "Ульяновск",
    ],
    points: [
      "Уфа1",
      "Уральск1",
      "Увельский1",
      "Ульяновск1",
      "Уфа",
      "Уральск",
      "Увельский",
      "Ульяновск",
    ],
  }),
  methods: {
    updateOptionPoint() {
      // Функция будет доставать нужное значение для опции Пункт выдачи
      const optionPoint = {
        type: "point",
        name: this.stepFields.point.name,
        value: this.stepFields.point.value,
        stepId: this.currentStep,
      };

      this.updateOptions(optionPoint);
    },
  },
  computed: {
    filteredOptions: () => {
      return this.stepOptions.filter(function (option) {
        return option.type !== "point";
      });
    },
    stepFields: {
      get() {
        return this.fields;
      },
      set(value) {
        this.$emit("update:fields", value);
      },
    },
  },
};
